.Filter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin: 10px;
  padding: 6px;
  border: 1px solid #2f91f2;
  border-radius: 3px;
  width: 220px;
  text-align: left;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.Icon {
  font-size: 1.2em;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.On {
  color: white;
  background-color: #2f91f2;
}

.Off {
  color: #2f91f2;
  background-color: white;
}



