.Wrapper {
  width: 300px;
  border: 2px solid #bbb;
  border-radius: 3px;
  cursor: pointer;
  -webkit-transition: height .2s;
  -o-transition: height .2s;
  transition: height .2s;
  margin-bottom: 5px;
}

.Dropdown > div {
  padding: 8px;
}

.Header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background-color: #f9f9f9;
  padding: 5px;
  color: #2D91F2;
}

.ListItem {
  color: black
}

.ErrorItem {
  color: #666;
  cursor: not-allowed;
}
.ListItemSelected {
  color: #2D91F2;
}
.ListItem:hover {
  color: #2D91F2;
}

.Background1 {
  background-color: #ddd;
}

.Background2 {
  background-color: white;
}
.Open {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  height: 100%;
  -webkit-transition: height .2s;
  -o-transition: height .2s;
  transition: height .2s;
  overflow-y: scroll;
}

.Close {
  height: 0px;
  -webkit-transition: height .2s;
  -o-transition: height .2s;
  transition: height .2s;
}

.Dropdown {
  overflow-y: hidden;
}



