.NavContainer {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  max-width: 1300px;
  background-color: #2D91F2;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 3px;
  -webkit-box-shadow: 0 12px 20px 3px rgba(0,0,0,0.14), 0 9px 46px 8px rgba(0,0,0,0.12), 0 11px 15px -7px rgba(0,0,0,0.2);
          box-shadow: 0 12px 20px 3px rgba(0,0,0,0.14), 0 9px 46px 8px rgba(0,0,0,0.12), 0 11px 15px -7px rgba(0,0,0,0.2);
}

.NavList{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-width: 600px;
  margin-bottom: 0;
}

@media screen (min-width: 1300px) {
  .NavContainer {
    border-radius: 0px;
  }
}



